.home {
    .main-img {
        width: 100%;
        background-image: url('https://images.unsplash.com/photo-1451187580459-43490279c0fa?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1952&q=80');
        background-repeat: no-repeat;
        background-size: cover;
        height: 100vh;
        color: $clr4;

        .main-info{
            transition: all .5s;
        }

        h2 {
            margin-top: 60px;
        }

        * {
            padding-top: 25px;
            padding-left: 10px;
        }

        span {
            animation: span-animation 1s infinite ;
        }
        
        button {
            padding: 10px;
            margin-top: 25px;
            background-color: rgba(0, 0, 0, 0.329);
            color: $clr4;
            border: 2px solid $clr4;
            border-radius: 5px;
            cursor: pointer;
            font-size: 50px;
            position: absolute;
            right: 50%;
            top: 50%;
            transform: translate(50%, -50%);
            transition: .5s;
            z-index: 0;

            &:hover {
                font-size: 55px;
            }
        }
    }
}

@media(max-width:940px) {
    .home {

        h1 {
            font-size: rem(50px);
        }
        
    }
}
