@media(max-width:700px) {
    .home {

        h1 {
            font-size: rem(35px);
        }
    }
}
@media(max-width:500px) {
    .home {

        h1 {
            font-size: rem(24px);
        }
    }
}

@keyframes span-animation {
    0% {
        opacity: 1;
    }
    
    33% {
        opacity: 0;
        
    }
    66% {
        opacity: 1;
    }
    
    100% {
        opacity: 0;

    }
}