.specialties {
    background-color: $clr5;
    text-align: center;

    .main-list {
        overflow-x: hidden;
        align-items: start;
        justify-content: left;

        .main-titles {
            cursor: pointer;
            padding: 120px 0;
            background-color: $clr5;
            transition: width .5s;
            width: 25%;
            height:  100vh;
            box-shadow: inset 0px 0px 12px -10px #000000;

            h2 {
                transform: rotate(-90deg);
            }
            
            ul {
                height: 100%;
                
                opacity: 0;
                
                li {
                    width: 0;
                }
            }
            
            &.choosen {
                background-color: #fff;
                width: 50%;
                height: 100vh;
                ;
                transition: width .5s;
                cursor: default;
                
                h2 {
                    margin-bottom: 70px;
                    transform: rotate(0deg);
                    transition: transform .5s;
                }

                ul {
                    transition: opacity .5s;
                    opacity: 1;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: space-between;

                    li{
                        transition: width .5s;
                        width: 100px;
                        @media(max-height:736px){
                            width: 80px;
                        }
                        @media(max-height:641px){
                            width: 60px;
                        }
                        @media(max-height:545px){
                            width: 40px;
                        }
                        @media(max-height:450px){
                            width: 28px;
                        }
                    }
                }
            }
        }

    }
}


@media(max-width:500px) {
    .specialties {
        .main-list {
            .main-titles {

                h2 {
                    font-size: 20px;
                }

                ul {

                    li {

                        font-size: 50px;
                    }
                }


            }

        }
    }
}